<template>
    <yd-layout>
        <div class="reviewTop">
            <div class="rtLine">{{detail.KpiName}} — {{detail.UserName}}</div>
            <div class="rtLine2">状态：{{detail.Status}}</div>
            <div class="appraise2">个人指标</div>
        </div>
        <div class="rdScore" v-if="leaderScore.Score!=null">
            <yd-flexbox>
                <yd-flexbox-item>直接上级评价/评分：</yd-flexbox-item>
                <div>{{leaderScore.Score}}</div>
            </yd-flexbox>
            <yd-flexbox>
                <div class="comment" v-html="leaderScore.Comment"></div>
            </yd-flexbox>
        </div>
        <div class="instruction">
            <yd-flexbox>
                <div class="pishi">批示:</div>
                <div class="pass" :class="{p_active:Result==true}" @click="Result=true"><span></span>审核通过</div>
                <div class="noPass" :class="{p_active:Result==false}" @click="Result=false"><span></span>审核不通过</div>
            </yd-flexbox>
        </div>
        <div class="inputScore">
            <div class="s_trs">
                <yd-flexbox>
                    <div class="div_pj">评价：</div>
                    <trsTextarea placeholder="请填写您的评价" v-model="comment"></trsTextarea>
                    <div class="save" @click="saveData()" v-if="ScoreType==0">提交</div>
                    <div class="save" @click="saveData()" v-if="ScoreType==1&&SeniorScoreWeight==0&&status.indexOf('间接')!=-1&&Result==false">提交</div>
                    <div class="save" @click="saveData()" v-if="ScoreType==1&&SeniorScoreWeight==0&&status.indexOf('间接')!=-1&&Result==true">保存</div>
                </yd-flexbox>
            </div>
            <div class="s_trs2" v-if="ScoreType==1&&(status.indexOf('直接')!=-1||(SeniorScoreWeight>0&&status.indexOf('间接')!=-1))">
                <yd-flexbox>
                    <div class="getScorse">打分：</div>
                    <trsTextarea v-model="score" class="trsClass"></trsTextarea>
                    <div class="save saveWidth" @click="saveData()" v-if="Result==false">提交</div>
                    <div class="save saveWidth" @click="saveData()" v-if="Result==true">保存</div>
                    <!-- <div class="save" @click="saveData()" v-if="leaderScore.Score!=null">保存</div>
                    <div class="save submit" v-if="leaderScore.Score!=null">提交</div> -->
                </yd-flexbox>
            </div>
        </div>
        <div class="oneInfo" v-for="(item, index) in detail.ItemList" :key="index">
            <div class="infoWork">
                <div class="iw_title">{{index+1}}.工作任务：</div>
                <div class="infoDetail" v-html="transferString(item.PlanedWork)" v-if="item.PlanedWork!=null"></div>
            </div>
            <div class="infoWork finshed">
                <div class="iw_title">完成情况：</div>
                <div class="infoDetail" v-html="transferString(item.ActualWork)" v-if="item.ActualWork!=null"></div>
            </div>
        </div>
        <yd-tabbar class="bot_perf" slot="tabbar" bgcolor="#F4F4F2" activeColor="#369df4" color="#999999">
            <yd-tabbar-item link="" title="待办事项" active>
                <yd-icon name="grdb" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item title="我的考评" link="/myPerformance">
                <yd-icon name="wdkp" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    data() {
        return {
            infoWork: '1  根据调研计划，完成信息化建设调研<br>2  根据调研情况，完成调研总结，编写调研报告<br>3  启动完成信息化规划第二阶段蓝图设计和第三阶段实施举措，保障的编写。',
            Result: true,
            detail: {},
            score: '',
            comment: '',
            leaderScore: {},
            ScoreType: -1,
            prompt: '',
            SeniorScoreWeight: -1,
            status: ''
        }
    },
    mounted() {
        document.setTitle('绩效审核');
        let vm = this;
        vm.detailData();
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    methods: {
        detailData() {
            let vm = this;
            vm.get(vm.conf.api + '/HRMSApi/api/MonthlyKpi/GetSingleDoMonthlyKpi', {
                token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                Id: vm.$route.params.id
            }, function(res) {
                console.log(JSON.parse(res.JsonData));
                vm.detail = JSON.parse(res.JsonData);
                vm.leaderScore = vm.detail.LeaderScore;
                vm.ScoreType = vm.detail.ScoreType;
                vm.SeniorScoreWeight = vm.detail.SeniorScoreWeight;
                vm.status = vm.detail.Status;
            }, function(res) {
                if (res.response.data.IsSuccess == false) {
                    vm.$dialog.toast({
                        mes: '该流程已处理',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/understrapper');
                        }
                    });
                }
            });
        },
        saveData() {
            let vm = this;
            if (vm.ScoreType == 0) {
                vm.prompt = '提交成功'
            } else if (vm.ScoreType == 1) {
                if (vm.Result == true) {
                    vm.prompt = '保存成功'
                } else {
                    vm.prompt = '提交成功'
                }
            }
            if (vm.comment == '') {
                vm.$dialog.toast({
                    mes: '请填写您的评价',
                    timeout: 2000
                });
            } else if (vm.score == '' && vm.ScoreType == 1 && vm.Result == true && vm.SeniorScoreWeight > 0) {
                vm.$dialog.toast({
                    mes: '请填写您的评分',
                    timeout: 2000
                });
            } else {
                vm.post(vm.conf.portalMobile + '/API/HRMSApi/api/MonthlyKpi/KpiEvaluate', {
                    token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    KpiId: vm.$route.params.id,
                    Result: vm.Result,
                    Comment: vm.comment,
                    Score: vm.score
                }, function(res) {
                    vm.$dialog.toast({
                        mes: vm.prompt,
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/understrapper');
                        }
                    });
                });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import './performance.less';
.yd-icon-grdb:before,
.yd-icon-wdkp:before {
    display: block;
    content: "";
}

.yd-icon-grdb:before {
    width: 0.41rem;
    height: 0.41rem;
    background: url(../../../static/images/performance/grdb@2x.png) no-repeat 0 0;
    background-size: 0.4rem;
}

.yd-icon-wdkp:before {
    width: 0.35rem;
    height: 0.35rem;
    background: url(../../../static/images/performance/wdkp.png) no-repeat 0 0;
    background-size: 0.35rem;
}

.saveWidth {
    width: 2.7rem!important;
}

.trsClass {
    width: 2.7rem!important;
}
</style>


